@tailwind base;
@tailwind components;
@tailwind utilities;

/* use https://fonts.google.com/ website for chose fonts */
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap');

* {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}

:root {
    --color-white: #fff;
    --color-gray:rgba(242,242,242);
    --color-black: #1A1A1A;
    --color-orange: #D87D4A;
    --color-orange-light: #FBAF85;
    --color-red: #e51919;
    --transition: all 400ms ease;
}

html {
    scroll-behavior: smooth;
}

::-webkit-scrollbar {
    /* display: none; */
}

body {
    font-family: 'Poppins', sans-serif;
    background-color: var(--color-white) !important;
    overflow-x: hidden;
}


/* ============ GENERAL STYLES =============  */

.navBox {
    display: flex;
    align-items: flex-end;
    justify-content: center;
}

h1,
h2,
h3,
h4,
h5 {
    font-weight: 700;
}

h1 {
    font-size: 28px;
    line-height: 36px;
    letter-spacing: 2px;
}

h2 {
  font-size: 24px;
  letter-spacing: .86px;
  line-height: 25px;
}

button {
  cursor: pointer;
}

img {
  object-fit: cover;
}

.img_contain {
  object-fit: contain;
}

.header_p {
  font-size: 14px;
  line-height: 19px;
  letter-spacing: 10px;
  color: var(--color-white);
  opacity: 49.64%;
  width: 100%;
}

.header_title {
  font-size: 56px;
  line-height: 58px;
  letter-spacing: 2px;
  color: var(--color-white);
}

.header_title_child {
  font-size: 40px;
  line-height: 44px;
  letter-spacing: 1.5px;
  color: var(--color-white);
}

.header_body {
  font-size: 16px;
  line-height: 25px;
  color: var(--color-white);
  opacity: .75;
}

.text_body {
  color: var(--color-black);
  font-size: 18px;
  line-height: 24px;
  letter-spacing: 1.13px;
  font-weight: 700;
}

.text_small {
  color: var(--color-black);
  font-size: 14px;
  line-height: 0;
  letter-spacing: 0;
  font-weight: 900;
}

.text_h1 {
  font-size: 40px;
    line-height: 44px;
    letter-spacing: 1.5px;
    font-weight: 700;
}

.text_body_white {
  color: var(--color-white);
  font-size: 16px;
  line-height: 25px;
  font-weight: 700;
}

.text_orange {
  color: var(--color-orange);
}

.text_orange_overLine {
  color: var(--color-orange);
  font-size: 14px;
    line-height: 19px;
    letter-spacing: 10px;
}

.text_gray {
  color:var(--color-black);
  font-size: 13px;
  line-height: 25px;
  letter-spacing: 2px;
  font-weight: 700;
  opacity: .50;
  display: flex;
  gap: 3px;
}

.text_gray:hover {
  color:var(--color-orange);
}

.btn_orange {
    width: max-content;
    max-height: 5rem;
    color: var(--color-white);
    background-color: var(--color-orange);
    padding: 1rem 2rem;
    transition: var(--transition);
}

.btn_orange:hover {
    background-color: var(--color-orange-light);
}

.btn_black {
  width: max-content;
  max-height: 5rem;
  color: var(--color-white);
  background-color: var(--color-black);
  padding: 1rem 2rem;
  border: 2px solid transparent;
}

.btn_black:hover {
  background-color: var(--color-white);
  border: 2px solid var(--color-black);
  color: var(--color-black);
}

.btn_transparent {
  width: max-content;
  max-height: 5rem;
  color: var(--color-black);
  background-color: transparent;
  padding: 1rem 2rem;
  border: 2px solid var(--color-black);
}

.btn_transparent:hover {
  background-color: var(--color-black);
  color: var(--color-white);
}

.btn_red {
  background: var(--color-red);
  color: var(--color-white);
  border: 2px solid var(--color-red);
  padding: 0.2rem .7rem .3rem;
  font-size: 17px;
  line-height: 24px;
  
}

.btn_red:hover {  
  background: var(--color-white);
  color: var(--color-black);
}

.opacity_50 {
  opacity: .50;
}

.opacity_75 {
  opacity: .75;
}

.icon1_5 {
    color: var(--color-primary);
    font-size: 1.5rem;
}

.icon1 {
    color: var(--color-primary);
    font-size: 1rem;
}

