}

.p_3 {
  padding: 3rem;
}

.p_4 {
  padding: 4rem;
}

.p_5 {
  padding: 5rem;
}

.p_6 {
  padding: 6rem;
}

.pt_0 {
  padding-top: 0 !important;
}

.pt_1 {
  padding-top: 1rem;
}

.pt_2 {
  padding-top: 2rem;
}

.pb_0 {
  padding-bottom: 0 !important;
} 

