  padding: 1rem;
}

.mr_a {
  margin: auto;
}

.ml_25 {
  margin-left: 2.5rem;
}

.mr_25 {
  margin-right: 2.5rem;
}

.w_50 {
  width: 50%;
}

.w_100 {
  width: 100%;
}

.space_between {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.boxRow {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.boxColumn {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.boxWrap {
  display: flex;
  flex-wrap: wrap;
}

.full {
  width: 100%;
  height: 100%;
}

.gap_1 {
  display: flex;
  gap: 1rem;
}

.gap_2 {
  display: flex;
  gap: 2rem;
}

.gap_3 {
  display: flex;
  gap: 3rem;
}

.bgGray {
  background-color: var(--color-gray);
}

.borderB_2 {
  border-bottom: 2px solid var(--color-white);
}

.loader {
    width: 1rem;
    height: 1rem;
    border: 0.1rem solid var(--color-primary);
    border-bottom-color: transparent;
    border-radius: 50%;
    display: inline-block;
    box-sizing: border-box;
    animation: rotation 1s linear infinite;
    }

    @keyframes rotation {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
} 

